@import url(https://fonts.googleapis.com/css2?family=Open+Sans:wght@300&display=swap);
/* --All fonst skall vara i googles font "Open Sans" */
/* Responsiv mobil, surfplatta m.m. */
/* Animation vid slumpning av brickor */

*, *::after, *::before{
    box-sizing: border-box;
    font-family: 'Open Sans', sans-serif;
}
body{
    background-color: rgba(0, 0, 0, 0.89);
    padding: 0;
    margin: 0;
}

.main_div{
    height: 100vh;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    background-image: linear-gradient(45deg, rgba(0, 0, 0, 0.7), rgba(0, 0, 0, 0.7)), url(/15puzzel/static/media/water.2fd537c0.jpg);
}

*:focus{
    outline: none;
}
.board{
    border-color: rgba(0, 0, 0, 0.5);
    border-width: 1vmin;
    border-style: solid;
    background-color: burlywood;
    background-image: linear-gradient(rgba(17, 17, 17, 0.5), rgba(153, 153, 153, 0.5)), url(/15puzzel/static/media/wood_block.13520c6a.jpg);
    background-size: cover;
    width: 60vmin;
    height: 60vmin;
    margin: 1vmin auto;
    display: table;
    table-layout: fixed;
    border-radius: 1vmin;
}
.board_row{
    display: table-row;
}
.size_input_div{
    margin: 0 auto;
    width: -webkit-fit-content;
    width: -moz-fit-content;
    width: fit-content;
    color: wheat;
}
.size_input_div label{
    font-size: 3vmin;
}
.size_input{
    font-size: 3vmin;
    font-weight: 900;
}

.shuffle{
    display: flex;
    justify-content: center;
    align-items: center;
    padding: 1%;
}
.shuffle_btn{
    font-size: 2vmin;
    font-weight: 900;
    padding: 2vmin;
    border-radius: 1vmin;
    background-color: rgba(0, 0, 0, 0);
    color: wheat;
    border: 2px solid darkgoldenrod;
    transition: all .5s ease-in-out;
}
.shuffle_btn:hover{
    background-color: darkgoldenrod;
    color: #111;
}

.winner_text{
    display: flex;
    flex-direction: column;
    justify-content: center;
    text-align: center;
    align-items: center;
    background-color: black;
    color: azure;
    opacity: 0.7;
    width: 61vmin;
    height: 61vmin;
    z-index: 100;
    position: absolute;
    margin: 0 auto;
    transform: translateY(-100%) scale(1);
    left: calc(50% - 30vmin);
    font-size: calc(1vw + 1vh + 1vmin );
    -webkit-animation: winner_animation 1s;
            animation: winner_animation 1s;
    border-radius: 1vmin;
}
@-webkit-keyframes winner_animation{
    from {transform: translateY(-100%) scale(0);}
    to {transform: translateY(-100%) scale(1);}
}
@keyframes winner_animation{
    from {transform: translateY(-100%) scale(0);}
    to {transform: translateY(-100%) scale(1);}
}

.no_brick{
    display: table-cell;
    color: rgba(0, 0, 0, 0);
    font-size: max(3vmin);
}

.brick{
    display: table-cell;
    text-align: center;
    vertical-align: middle;
    border: 1px solid rgba(0, 0, 0, 0.5);
    font-weight: 800;
    font-size: max(3vmin);
    background-image: linear-gradient(45deg, rgba(0, 0, 0, 0.1), rgba(51, 51, 51, 0.2)), url(/15puzzel/static/media/wood_block.13520c6a.jpg);
    position: relative;
    border-radius: 1vmin;
    box-shadow: 2px 2px black;
}

.move_left{
    -webkit-animation: animation_left 0.3s ease-out;
            animation: animation_left 0.3s ease-out;
}
.move_right{
    -webkit-animation: animation_right 0.3s ease-out;
            animation: animation_right 0.3s ease-out;
}
.move_up{
    -webkit-animation: animation_up 0.3s ease-out;
            animation: animation_up 0.3s ease-out;
}
.move_down{
    -webkit-animation: animation_down 0.3s ease-out;
            animation: animation_down 0.3s ease-out;
}

@-webkit-keyframes animation_left{
    from {transform: translateX(100%);}
    to {transform: translateX(0%);}
}

@keyframes animation_left{
    from {transform: translateX(100%);}
    to {transform: translateX(0%);}
}
@-webkit-keyframes animation_right{
    from {transform: translateX(-100%);}
    to {transform: translateX(0%);}
}
@keyframes animation_right{
    from {transform: translateX(-100%);}
    to {transform: translateX(0%);}
}
@-webkit-keyframes animation_up{
    from {transform: translateY(100%);}
    to {transform: translateX(0%);}
}
@keyframes animation_up{
    from {transform: translateY(100%);}
    to {transform: translateX(0%);}
}
@-webkit-keyframes animation_down{
    from {transform: translateY(-100%);}
    to {transform: translateX(0%);}
}
@keyframes animation_down{
    from {transform: translateY(-100%);}
    to {transform: translateX(0%);}
}

.shuffle_brick{
    transform-style: preserve-3d;
    -webkit-animation: animation_shuffle 0.5s ease-in-out;
            animation: animation_shuffle 0.5s ease-in-out;
}
@-webkit-keyframes animation_shuffle{
    
    0%{transform: rotateY(0deg) rotateZ(0deg);}
    15%{color: rgba(0, 0, 0, 0);}
    50%{transform: rotateY(60deg) rotateZ(60deg) scale(0);}
    98%{transform: rotateY(0deg) rotateZ(0deg);}
    99%{color: black;}
}
@keyframes animation_shuffle{
    
    0%{transform: rotateY(0deg) rotateZ(0deg);}
    15%{color: rgba(0, 0, 0, 0);}
    50%{transform: rotateY(60deg) rotateZ(60deg) scale(0);}
    98%{transform: rotateY(0deg) rotateZ(0deg);}
    99%{color: black;}
}

@media only screen and (max-width: 600px){
    .board{
        width: 90vmin;
        height: 90vmin;
    }
    .winner_text{
        width: 91vmin;
        height: 91vmin;
        left: calc(50% - 45vmin);
    }
}
